import React from "react"
import { graphql, Link } from "gatsby"
import Layout from "../components/layout"
import NewsletterForm from "../components/newsletterForm"
import NewsletterBox from "../components/newsletterBox"
import SEO from "../components/seo"
import {
  Container,
  Heading,
} from "react-bulma-components/full"

export default ({ data }) => {
  return (
    <Layout>
      <SEO title="Archiv" keywords={[`workshops`, `handmade`, `kurse`]} />
      <Container Fluid>
        <NewsletterForm />
          <Heading size={3} renderAs="h2">Archiv</Heading>
          {data.allJsonJson.edges.map(({ node }, index) => (
            <Link to={node.title}>
              <NewsletterBox node={node}/>
            </Link>
          ))}
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query {
    allJsonJson(
    sort: {
      fields: title
      order: DESC
    }
  ) {
      edges {
        node {
          published
          title
          tags
        }
      }
    }
  }
`
